import {Component, OnInit, Input} from '@angular/core';

@Component({
  selector   : 'trailer',
  template: require('./trailer.component.html'),
  styles: [require('./trailer.component.css')]
})

export class TrailerComponent implements OnInit{

  @Input() element:any;
  link:string;

  ngOnInit(){
    this.link=this.element.title.replace(/ /g,"+");
  }
}



